<template>
  <div class="page">
    <div class="head flex-center">
      <div class="head-back">
        <!-- <img src="@/assets/image/new/back.png" alt=""> -->
      </div>
      <div class="head-title">合約管理</div>
      <div class="head-right">
        <div class="head-wallet" v-if="!address" @click="initWallet">鏈接錢包</div>
        <div class="head-wallet" v-else>{{address.substring(0,4)+'...'+address.substring(address.length-4,address.length)}}</div>
      </div>
    </div>
    <div class="set-content">
      <div class="dqcan">kworld转移</div>
      <div class="set-item flex-between">
        <div class="dqcan">owner地址</div>
        <input class="flex-item" type="text" v-model="kworldownerAddress" placeholder="請輸入">
      </div>
      <el-button @click="confimkworldAddress()">保存</el-button>

      <div class="dqcan">seele理财转移</div>
      <div class="set-item flex-between">
        <div class="dqcan"> owner地址</div>
        <input class="flex-item" type="text" v-model="seeleownerAddress" placeholder="請輸入">
      </div>
      <el-button @click="confimseeleAddress()">保存</el-button>

      <div class="dqcan">seele跨连桥转移</div>
      <div class="set-item flex-between">
        <div class="dqcan">owner地址</div>
        <input class="flex-item" type="text" v-model="seele2ownerAddress" placeholder="請輸入">
      </div>
      <el-button @click="confimseele2Address()">保存</el-button>

      <div class="dqcan">metavisa转移</div>
      <div class="set-item flex-between">
        <div class="dqcan">owner地址</div>
        <input class="flex-item" type="text" v-model="metaownerAddress" placeholder="請輸入">
      </div>
      <el-button @click="confimmetaAddress()">保存</el-button>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch } from "vue";
import { useStore } from "vuex";
import config from "@/utils/config";
import web3Data from "@/utils/initWeb3";
import { ElMessage, ElNotification } from 'element-plus'
import MainChainDataAbi from "@/utils/MainChainDataAbi";
import Bignumber from "bignumber.js";

export default({
  setup () {
    const store = useStore()
    const data = reactive({
      local: computed(() => store.state.locale),
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      kworldContract: null,
      seeleContract: null,
      seele2Contract: null,
      metaContract: null,
      kworldownerAddress: '',
      seeleownerAddress: '',
      seele2ownerAddress: '',
      metaownerAddress: '',
      initContract: () => {
        data.kworldContract = new data.web3.eth.Contract(MainChainDataAbi, config.kworldAddress)
        data.seeleContract = new data.web3.eth.Contract(MainChainDataAbi, config.seeleAddress)
        data.seele2Contract = new data.web3.eth.Contract(MainChainDataAbi, config.seele2Address)
        data.metaContract = new data.web3.eth.Contract(MainChainDataAbi, config.metaAddress)
      },
      confimkworldAddress: async()=> {
        let id = await web3Data.web3.eth.net.getId()
        if(id != 1) {
          ElNotification.error({
            title: '網路錯誤',
            message: '請將網路切換到ETH链'
          })
          return false;
        }
        let isAddress = await data.web3.utils.isAddress(data.kworldownerAddress)
        if (!isAddress) {
          ElNotification.error({
            title: '請輸入正確的地址',
            duration: 3000
          });
          return;
        }
        let gas = await data.kworldContract.methods.transferOwnership(data.kworldownerAddress).estimateGas({from: data.address})
        console.log('gas', gas)
        await data.kworldContract.methods.transferOwnership(data.kworldownerAddress).send({from: data.address, gas: gas})
        .then(() => {
          ElNotification.success({
            title: '成功',
            duration: 3000
          })
          data.kworldownerAddress = null
        }).catch(() => {
          ElNotification.error({
            title: '失敗',
            duration: 3000
          })
        })
      },
      confimseeleAddress: async()=> {
        let id = await web3Data.web3.eth.net.getId()
        if(id != 186) {
          ElNotification.error({
            title: '網路錯誤',
            message: '請將網路切換到SEELE链'
          })
          return false;
        }
        let isAddress = await data.web3.utils.isAddress(data.seeleownerAddress)
        if (!isAddress) {
          ElNotification.error({
            title: '請輸入正確的地址',
            duration: 3000
          });
          return;
        }
        let gas = await data.seeleContract.methods.transferOwnership(data.seeleownerAddress).estimateGas({from: data.address})
        console.log('gas', gas)
        await data.seeleContract.methods.transferOwnership(data.seeleownerAddress).send({from: data.address, gas: gas})
        .then(() => {
          ElNotification.success({
            title: '成功',
            duration: 3000
          })
          data.seeleownerAddress = null
        }).catch(() => {
          ElNotification.error({
            title: '失敗',
            duration: 3000
          })
        })
      },
      confimseele2Address: async()=> {
        let id = await web3Data.web3.eth.net.getId()
        if(id != 1) {
          ElNotification.error({
            title: '網路錯誤',
            message: '請將網路切換到ETH链'
          })
          return false;
        }
        let isAddress = await data.web3.utils.isAddress(data.seele2ownerAddress)
        if (!isAddress) {
          ElNotification.error({
            title: '請輸入正確的地址',
            duration: 3000
          });
          return;
        }
        let gas = await data.seele2Contract.methods.transferOwnership(data.seele2ownerAddress).estimateGas({from: data.address})
        console.log('gas', gas)
        await data.seele2Contract.methods.transferOwnership(data.seele2ownerAddress).send({from: data.address, gas: gas})
        .then(() => {
          ElNotification.success({
            title: '成功',
            duration: 3000
          })
          data.seele2ownerAddress = null
        }).catch(() => {
          ElNotification.error({
            title: '失敗',
            duration: 3000
          })
        })
      },
      confimmetaAddress: async()=> {
        let id = await web3Data.web3.eth.net.getId()
        if(id != 1) {
          ElNotification.error({
            title: '網路錯誤',
            message: '請將網路切換到ETH链'
          })
          return false;
        }
        let isAddress = await data.web3.utils.isAddress(data.metaownerAddress)
        if (!isAddress) {
          ElNotification.error({
            title: '請輸入正確的地址',
            duration: 3000
          });
          return;
        }
        let gas = await data.metaContract.methods.transferOwnership(data.metaownerAddress).estimateGas({from: data.address})
        console.log('gas', gas)
        await data.metaContract.methods.transferOwnership(data.metaownerAddress).send({from: data.address, gas: gas})
        .then(() => {
          ElNotification.success({
            title: '成功',
            duration: 3000
          })
          data.metaownerAddress = null
        }).catch(() => {
          ElNotification.error({
            title: '失敗',
            duration: 3000
          })
        })
      },
      initWallet: () => {
        web3Data.initWeb3()
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
})
</script>
<style lang="scss" scoped>
.page{
  background: #060303;
}
.head{
  background: transparent;
  height: 44px;
  line-height: 44px;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  color: #03090F;
  padding: 0 15px;
  margin-bottom: 15px;
  position: relative;
  .head-back{
    img{
      width: 9px;
      height: 16px;
    }
  }
  .head-title{
    font-size: 18px;
    font-weight: bold;
    color: #FFFFFF;
  }
  .head-wallet{
    font-size: 15px;
    font-weight: bold;
    color: #FFFFFF;
  }
  .head-right{
    position: absolute;
    right: 15px;
    top: 0;
    max-width: 100px;
  }
}
.set-content{
  padding: 15px;
  background: #F4F4F4;
  border-radius: 15px 15px 0px 0px;
  .dqcan{
    font-size: 15px;
    font-weight: bold;
    color: #333333;
  }
  .set-item{
    background: #FFFFFF;
    border-radius: 10px;
    padding: 5px 12px;
    height: 55px;
    line-height: 55px;
  }
  input{
    // width: 80%;
    // height: 50px;
    background: transparent;
    border: 0;
    text-align: right;
    font-size: 15px;
    color: #333;
    margin-left: 10px;
  }
  .Precision{
    text-align: right;
    font-size: 15px;
    color: #333;
    margin-top: 10px;
  }
  .el-button{
    width: 100%;
    height: 50px;
    margin: 30px 0 30px;
    background: linear-gradient(90deg, #FAB742, #FCCC57);
    border-radius: 25px;
    font-size: 18px;
    font-weight: bold;
    color: #333333;
  }
}
</style>
